export function parseParties(party) {
  let puolue = party
  switch (party) {
    case 'Keskustan eduskuntaryhm':
      puolue = 'kesk'
      break
    case 'Perussuomalaisten eduskuntaryhm':
      puolue = 'ps'
      break
    case 'Kansallisen kokoomuksen eduskuntaryhm':
      puolue = 'kok'
      break
    case 'Sosialidemokraattinen eduskuntaryhm':
      puolue = 'sdp'
      break
    case 'Vihre eduskuntaryhm':
      puolue = 'vihr'
      break
    case 'Vasemmistoliiton eduskuntaryhm':
      puolue = 'vas'
      break
    case 'Ruotsalainen eduskuntaryhm':
      puolue = 'rkp'
      break
    case 'Kristillisdemokraattinen eduskuntaryhm':
      puolue = 'kd'
      break
    default:
      break
  }
  return puolue
}
export default { vastausKategoriat, puolueet, parseParties }
