class UusiKategoria extends React.Component {
    onSubmit = async (e) => {
      e.preventDefault();
      const kategoria = {
        nimi: e.target.kategoria.value,
      };
      e.target.kategoria.value = '';
      const katet = Object.keys(vastausKategoriat)
      const vastaus = katet.find(x => x === kategoria.nimi.toLowerCase())
      if (vastaus) {
        const vastaukset = vastausKategoriat[vastaus]
        kategoria.vastaukset = vastaukset
      }
      try {
        const loggedUserJSON = window.localStorage.getItem('loggedUser');
        kategoriaService.setToken(JSON.parse(loggedUserJSON).token);
        await kategoriaService.addKategoria(kategoria);
        window.location.assign('/kategoriat');
      } catch (exception) {
        console.log('Tapahtui virhe');
      }
    }

    render() {
      return (
        <Form onSubmit={this.onSubmit}>
          <Form.Input
            label="Listtv kategoria"
            placeholder="Kategoria"
            name="kategoria"
          />
          <Button inverted color="green" type="submit">Tallenna</Button>

        </Form>

      );
    }
}

export default UusiKategoria;
